@import "./utils/vars";

.contactus {
  width: 280px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;

  @media (min-width: 743px) {
    width: 600px;
  }

  @media (min-width: 1259px) {
    width: 1020px;
  }

  &-title {
    color: $font-blackish;
    text-align: center;
    font-family: Inter, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: -1.5px;

    @media (min-width: 743px) {
      font-size: 40px;
      line-height: 40px;
    }

    @media (min-width: 1259px) {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__information {
    &__form {
      color: #7e7e83;
      font-family: Inter, sans-serif;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 20px;

      &-element {
        width: 100%;
        height: 48px;
        border: $font-white solid 1px;
        flex-shrink: 0;
        border-radius: 40px;
        background: $font-white;
        margin-bottom: $default-size;
      }

      &-message {
        display: flex;
        width: 280px;
        align-items: flex-start;
        border-radius: $default-size;
        background: $font-white;
      }
    }

    &-button {
      margin-top: 32px;
      color: #fff;
      text-align: center;
      font-family: Inter, sans-serif;
      font-size: $default-size;
      font-style: normal;
      font-weight: 700;
      line-height: 27px;
      display: flex;
      width: 280px;
      height: 56px;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      border: 2px solid black;
      background-color: black;
      border-radius: 40px;

      &:hover {
        background-color: white;
        color: black;
        cursor: pointer;
        transition-duration: 500ms;
      }
    }

    &__data {
      margin-top: 48px;
      margin-bottom: 96px;

      &-title {
        color: #0e0f11;
        font-family: Inter, sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
      }

      &-content {
        color: #3e3e3e;
        font-family: Inter, sans-serif;
        font-size: $default-size;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
        text-decoration: none;

        &:hover {
          color: aquamarine;
        }
      }
    }
  }

  &-frame {
    display: none;
  }
}
